import React, { Component } from "react";
// import logo from "./logo.svg";
// import Option from "./Components/Option";
import Questionare from "./Components/Questionare";
import Details from "./Components/Details";
import "./App.css";

class App extends Component {
  constructor(props) {
    super();
    this.state = {
      completedQues: false,
      userDetails: {},
    };
  }
  onComplete = (compStatus, userDetails) => {
    this.setState({
      completedQues: compStatus,
      userDetails: userDetails,
    });
  };
  render() {
    return (
      <div className="App">
        {/* <Option optionText="What is your name?" /> */}
        {this.state.completedQues === false ? (
          <Questionare onComplete={this.onComplete} />
        ) : (
          <Details
            userId={this.state.userDetails.userId}
            location={this.state.userDetails.location}
            probability={this.state.userDetails.probability}
          />
        )}
      </div>
    );
  }
}

export default App;
