import React, { Component } from "react";
import "./Questionare.css";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import NewReleasesIcon from "@material-ui/icons/NewReleases";

class Questionare extends Component {
  constructor(props) {
    super();
    this.state = {
      userId: 50830,
      username: "Alex Gomes",
      location: "Vashi",
      probability: 0,
    };
  }
  componentDidMount() {
    console.log(this.state);
    this.setState({
      username:
        this.props.username === undefined
          ? this.state.username
          : this.props.username,
      location:
        this.props.location === undefined
          ? this.state.location
          : this.props.location,
      probability:
        this.props.probability === undefined
          ? this.state.probability
          : this.props.probability,
      userId:
        this.props.userId === undefined ? this.state.userId : this.props.userId,
    });
  }

  render() {
    return (
      <div
        className="mt-3 p-3"
        style={{
          backgroundColor: "#f5f5f7",
          maxWidth: "800px",
          margin: "auto",
          border: "solid 1px #0071e3",
          borderRadius: "15px",
        }}
      >
        <div
          style={{
            maxWidth: "800px",
            margin: "auto",
            border: "solid 1px #e3e3e5",
            borderRadius: "15px",
            minHeight: "80vh",
          }}
        >
          <div
            className="mt-3 mb-3"
            style={{ fontSize: "2.5rem", fontWeight: 500 }}
          >
            Screening Results
          </div>
          <div
            className="mt-4"
            style={{
              maxWidth: "500px",
              textAlign: "left",
              margin: "auto",
              height: "100%",
            }}
          >
            <div className="nameDetail detail">
              UserId: <span style={{ flexGrow: 1 }}></span> {this.state.userId}
            </div>
            <div className="nameDetail detail">
              Name: <span style={{ flexGrow: 1 }}></span> {this.state.username}
            </div>
            <div className="locDetail detail">
              Location: <span style={{ flexGrow: 1 }}></span>
              {this.state.location}
            </div>
            <div className="probDetail detail">
              Probability: <span style={{ flexGrow: 1 }}></span>{" "}
              {this.state.probability.toFixed(2)}%
            </div>
          </div>
          <div
            className="mt-4 p-2"
            style={{
              maxWidth: "500px",
              margin: "auto",
              display: "flex",
              flexFlow: "column",
              height: "100%",
            }}
          >
            {this.state.probability < 30 ? (
              <>
                <div className="resultText"> You are safe for currently.</div>
                <div className="resultText">
                  Relax, Enjoy and Stay in Quarantine!
                </div>
                <div
                  className="mt-4"
                  style={{
                    backgroundColor: "black",
                    flexGrow: 1,
                  }}
                ></div>
                <div>
                  <CheckCircleIcon
                    style={{ fontSize: "4rem", color: "#00e676" }}
                  />
                </div>
              </>
            ) : (
              <>
                <div
                  className="resultText"
                  style={{ textAlign: "center", fontWeight: "600" }}
                >
                  Don't panic, we are here for you!
                </div>
                <div className="resultText">
                  Kindly go to Pooled Blood Test Tab (Stage 2). Get your blood
                  test done in an extremely efficient and quick manner by
                  joining your nearest pool.
                </div>
                <div
                  className="mt-4"
                  style={{
                    backgroundColor: "black",
                    flexGrow: 1,
                  }}
                ></div>
                <div>
                  <NewReleasesIcon
                    style={{ fontSize: "4rem", color: "#ff4343" }}
                  />
                </div>
              </>
            )}
          </div>
        </div>
        <div
          className="mt-2"
          style={{
            margin: "auto",
            display: "flex",
            justifyContent: "center",
          }}
        >
          {this.state.probability > 30 ? (
            <a href="/pooling" className="m-2">
              <span className="ques-btn p-2 pl-3 pr-3 ">
                Proceed to Stage 2
              </span>
            </a>
          ) : (
            <a href="/ngoconnect" className="m-2">
              <span className="ques-btn p-2 pl-3 pr-3 ">Donate</span>
            </a>
          )}
        </div>
      </div>
    );
  }
}
export default Questionare;
